
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
p, a {
    padding: 0;
    margin: 0;
}p, a {
    padding: 0;
    margin: 0;
}
.left-box {
    width: 34%;
    overflow: hidden;
    width: 60vw;
    border: solid 1px;
}
.btn-list{
    overflow: hidden;
    line-height: 30px;
    padding: 10px 0 0 0;
}
.btn-list button{
    margin-left: 5px;
}
.contant {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.right-box {
    width: 40vw;
}
.result-box{
    height: 50vh;
}
.result-item{
    text-align: left;
    height: 50vh;
    overflow-y: auto;
}
.result-title {
    font-size: 18px;
    font-weight: 700;
    padding: 5px;
}
.result-item span{
    display: inline-block;
    width: 13vw;
    text-align: center;
    margin-bottom: 10px;
}
.four{
    color: #0067ed;
}
.five{
    color: red;
}
.hello .echars-box, .hello .echars-box>div, .hello .canvas {
    width: 100vw;
    height: 30vh;
    overflow: hidden;
}
.show-hightProbability {
    border: solid 1px black;
    color: #6a09ff;
    min-height: 20px;
}

.echars-box {
    width: 800px;
    height: 300px;
}
